import React from "react";
import { StyledLoadMore } from "./style";

const LoadMore = ({ isLoading, loadMore, showLoadMore }) => {
  const handleClick = () => {
    if (!isLoading) {
      loadMore();
    }
  };

  return (
    <>
    <StyledLoadMore>
      {showLoadMore && (
        <button className="load-more" onClick={handleClick}>
          {isLoading ? "Loading..." : "Load More"}
        </button>
      )}
      </StyledLoadMore>
    </>
  );
};

export default LoadMore;
